nav {
  position: sticky;
  top: 0;
  background-color: navy;
  width: 100%;
  margin-top: 0;
  margin-left: 0;
  margin-bottom: 10px;
  color: white;
  font-size: 50px;
}

.logOutButton {
  position: absolute;
  top: 20px;
  right: 30px;
}

.logOutButton:hover {
  text-decoration-line: underline;
  cursor: pointer;
}

.HomeButton:hover {
  text-decoration-line: underline;
  cursor: pointer;
}

.HomeButton:visited {
  color: white;
}

.HomeButton {
  margin-right: 20px;
}
